.accordion {
  @apply flex justify-between flex-wrap m-0 p-0 mt-8 list-none mobile:mt-7;
}

.accordionItem {
  @apply border-b border-b-[#1E282D] w-full max-w-[360px] mobile:max-w-none;
}

.accordionItemTitle {
  @apply w-full m-0;
}

.accordionItemBtn {
  @apply flex items-center w-full bg-white cursor-pointer border-0 py-[15px] text-lg font-medium mobile:text-base text-left;
}

.accordionItemBtn svg {
  @apply flex-shrink-0 ml-auto bg-no-repeat transition-transform;
}

.accordionItem.active svg {
  @apply transform -rotate-180;
}

.accordionItemContainer {
  @apply overflow-hidden transition-height duration-200 ease-in-out;
}

.accordionItemContent {
  @apply border-t-[#1E282D] pb-2 text-sm;
}
